	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			keyWord:"",	//搜索关键字
			currentLoadFlag:true,
			newsList: [],	//全部宝贝、新品、活动数组集合
			storeId: '',	// 本店ID
			storeInfo: {
				merInfo:{
					views:0
				}
			},	//店铺首页
			tabIndex: 0,	//当前下标
			scrollInto: '',	//scroll跳转到这个Id
			isKeep: false,  //收藏
			tabBars: [
				{
					title: '店铺首页',
					id: 'shouye'
				},
				{
					title: '全部宝贝',
					id: 'baobei'
				},
				{
					title: '新品上架',
					id: 'xingping'
				},
				{
					title: '活动促销',
					id: 'huodong'
				}
			]
		};
	},
	onLoad(e) {
		this.storeId = e.storeId;
		this.getStoreInfo();
		this.initInfo();
	},
	methods: {
		//初始化newsList数据类型
		initInfo() {
			this.tabBars.forEach(tabBar => {
				this.newsList.push({
					data: [],
					refreshText: '',
					loadingStatus:"more",
					currentPage: 1,
					pageSize: 6,
					loadingFlag:false,
				});
			});
		},
		// 加载更多，
		loadMore() {
			if(this.currentLoadFlag){
				this.currentLoadFlag=false;
				this.newsList[this.tabIndex].loadingStatus="loading";
				this.newsList[this.tabIndex].loadingFlag=true;
				this.getList(this.tabIndex);
			}
			//保证每次只能请求一次
			setTimeout(()=>{
				this.currentLoadFlag=true;
			}, 500);
		
		},
		//导航栏点击跳转
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.tabIndex = index;
		},
		//左右滑动
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;
			//获取信息
			if (this.newsList[index].data.length === 0) {
				this.getList(index);
			}
		},
		
		//获取全部宝贝、新品、活动促销
		getList(index) {
			console.log('请求', index);
			let that = this;
			// 如果是首页点击那么不需要请求
			if (index == 0) {
				return;
			}
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'get_product_list',
					q: {
						mid: that.storeId,
						news: index == 2 ? 1 : '',
						benefit: index == 3 ? 1 : '',
						page: that.newsList[index].currentPage,
						limit: that.newsList[index].pageSize
					}
				}),
				function(res) {
					if (res.data.length == 0) {
						that.newsList[index].loadingStatus = 'noMore';
						return;
					}
					that.newsList[index].data = [...that.newsList[index].data, ...res.data];
					that.newsList[index].currentPage++;
					console.log(that.newsList[index]);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//获取店铺详情
		getStoreInfo() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'merchant_api',
					a: 'merchant_info',
					q: {
						id: that.storeId
					}
				}),
				function(res) {
					that.storeInfo = res.data;
					console.log(res);
					that.isKeep = res.data.merInfo.is_collect;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//收藏
		keep() {
			let that = this;
			//收藏商品之前判断用户是否已经登录
			if (this.isLogin()) {
				if (this.isKeep != null) {
					//收藏的情况下，进行取消收藏操作
					that.basePost(
						that.U({ c: 'merchant_api', a: 'uncollect_merchant' }),
						{
							merId: that.storeId
						},
						function(res) {
							if(res.code==200){
								that.isKeep = null;
							}
						},
						function(res) {
							console.log(res);
						}
					);
				} else {
					//还没有收藏的情况下，进行收藏操作
					that.basePost(
						that.U({ c: 'merchant_api', a: 'collect_merchant' }),
						{
							merId: that.storeId
						},
						function(res) {
							if(res.code==200){
								that.isKeep = '1';
							}
						},
						function(res) {
							console.log(res);
						}
					);
				}
			}
		},

		//返回上一页
		back() {
			uni.navigateBack();
		},
		// 搜索商品
		search() {
			console.log();
			uni.navigateTo({
				url: `/pages/list/search_list/search_list?keyWord=${this.keyWord}&storeId=${this.storeId}`
			});
		},
		// 商品详情
		detail(e) {
			if(e.type==2){
				uni.navigateTo({
					url: '/pages/list/line_details/line_details?id='+e.id
				});
			}else{
				uni.navigateTo({
					url: '/pages/list/goode_details/goode_details?id='+e.id
				});
			}
			
		},
	}
};
